import React, { createContext, useState, useMemo } from 'react';

export const MovieContext = createContext();

export const MovieProvider = ({ children }) => {
  const [movies, setMovies] = useState([]);
  const [favorites, setFavorites] = useState([]); // Lista de favoritos
  const [loading, setLoading] = useState(false);

  // Função para adicionar filme aos favoritos
  const addMovieToFavorites = (movie) => {
    console.log("Adicionando filme aos favoritos:", movie); // Log do filme que está sendo adicionado
    setFavorites((prevFavorites) => [...prevFavorites, movie]);
  };

  // Função para remover filme dos favoritos
  const removeMovieFromFavorites = (movieId) => {
    console.log("Removendo filme com ID:", movieId); // Log do ID que está sendo removido
    setFavorites((prevFavorites) => {
      const newFavorites = prevFavorites.filter((movie) => movie.id !== movieId);
      console.log("Novos favoritos após remoção:", newFavorites); // Log dos favoritos restantes
      return newFavorites; // Retorna a nova lista de favoritos
    });
  };

  const value = useMemo(() => ({
    movies, setMovies, loading, setLoading, favorites, addMovieToFavorites, removeMovieFromFavorites
  }), [movies, loading, favorites]);

  return (
    <MovieContext.Provider value={value}>
      {children}
    </MovieContext.Provider>
  );
};